import React from 'react'

class Publishing extends React.Component {
    render(){
        const { image, title, description } = this.props
        const styles = {
            backgroundSize: 'center center'
        }
        return(
            <div className="postsStyles flex-container">
                <figure>
                    <img src={image} style={styles}>
                    </img>
                </figure>
                <div className="postsTextStyles flex-container">
                    <h2>{title}</h2>
                    <p>{description}</p>
                    <button>GET IN</button>
                </div>
            </div>
        )
    }
} 

export default Publishing