import React from 'react'
import Publishing from './posts'

function PostList(props){
    return(
        <div>
            { props.products.map((product) => {
                    return(
                        <Publishing 
                            image={product.image}
                            title={product.title}
                            description={product.description}
                        />
                    )
                })
            }
        </div>
    )
}

export default PostList