import React from 'react'

class Cards extends React.Component {
    render(){
        const { image , text, btnHTML } = this.props
        return(
            <figure className="cardsStyle flex-container">
                <img className="cardsImages" src={image}></img>
                <p className="contentCards">{text}</p>
                <button className="contentCards">{btnHTML}</button>
            </figure>
        )
    }
}

export default Cards