import React from 'react'
import NavItems from '../components/nav-items'


class Header extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            data: [{
                "id": 1,
                "title": "SHOP",
            },{
                "id": 2,
                "title": "OUR COFFEE",
            },{
                "id": 3,
                "title": "CONTACT US",
            }]
        }
    }
    render(){
        return(
            <header>
                <nav className="wrapper">
                    <NavItems
                        items={this.state.data}
                    />
                </nav>
                <div id="intro-content" className="wrapper">
                    <h1>BALZAC’S <span id="coffee-text">COFFEE</span></h1>
                    <p>Be the first to know when we’re shipping.</p>
                </div>
            </header>
        )
    }
}

export default Header