import React from 'react'
import Cards from './cards'

function CardsList(props){
    return(
        <div id="cardsContainer" className="flex-container">
            { props.items.map((item) => {
                    return(
                        <Cards 
                            image={item.image}
                            text={item.text}
                            btnHTML={item.btnHTML}
                        />
                    )
                })
            }
        </div>
    )
}

export default CardsList 