import React from 'react'
import Footer from './footer'

class FooterItems extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            data: [{
                "id": 1,
                "itemMenu": "Home",
            },
            {
                "id": 2,
                "itemMenu": "Products",
            },
            {
                "id": 3,
                "itemMenu": "Jobs",
            },
            {
                "id": 4,
                "itemMenu": "Email us",
            }],
        }
    }
    render(){
        return(
            <footer className="flex-container">
                <div className="wrapper">
                    <ol className="listFooter flex-container">
                        {   this.state.data.map(item => {
                                return(
                                    <Footer
                                        itemMenu={item.itemMenu}
                                    />
                                )
                            })
                        }
                    </ol>
                    <a href="https://twitter.com/saezjuliann"><span className="fab fa-twitter"></span></a>
                    <a href="https://www.instagram.com/saezjuliann/"><span className="fab fa-instagram"></span></a>
                </div>
                <div id="secondSectionFooter" className="wrapper">
                    <form id="formularioOffers" className="flex-container">
                        <label>TAKE MY EMAIL TO NOTICE ME ALL THE OFFERS</label>
                        <input type="email" id="email" placeholder="email@email.com"></input>
                    </form>
                    <h4 id="copyright">© 2021, Developed by Julian Saez</h4>
                </div>
            </footer>
        )
    }
}

export default FooterItems