import React from 'react'
import PostList from '../components/postsList'
import Cards from '../components/cards'
import CardsList from '../components/cardsList'
import BoxesList from '../components/boxesList'


class Main extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            data: [{
                "id": 1,
                "image": "https://images.unsplash.com/photo-1447933601403-0c6688de566e?crop=entropy&cs=srgb&fm=jpg&ixid=MXw3MjAxN3wwfDF8c2VhcmNofDZ8fGNvZmZlZXxlbnwwfHx8&ixlib=rb-1.2.1&q=85&q=85&fmt=jpg&crop=entropy&cs=tinysrgb&w=450",
                "title": "OUR COFFES ALTERNATIVES",
                "description": "We understand, respect and celebrate every step of the crop to cup journey. We take great pride and attention in perfecting our craft."
            }],
            cards:[{
                "id": 1,
                "image": "https://images.unsplash.com/photo-1511920170033-f8396924c348?crop=entropy&cs=srgb&fm=jpg&ixid=MXw3MjAxN3wwfDF8c2VhcmNofDh8fGNvZmZlfGVufDB8fHw&ixlib=rb-1.2.1&q=85&q=85&fmt=jpg&crop=entropy&cs=tinysrgb&w=450",
                "text": "Nuestros selectos cafes nos hacen únicos en la zona.",
                "btnHTML": "Conocer"
            },{
                "id": 2,
                "image": "https://images.unsplash.com/photo-1510972527921-ce03766a1cf1?crop=entropy&cs=srgb&fm=jpg&ixid=MXw3MjAxN3wwfDF8c2VhcmNofDIyfHxjb2ZmZWV8ZW58MHx8fA&ixlib=rb-1.2.1&q=85&q=85&fmt=jpg&crop=entropy&cs=tinysrgb&w=450",
                "text": "Lugares de encuentro donde el aroma a cafe invade tu mente.",
                "btnHTML": "Conocer"
            },{
                "id": 3,
                "image": "https://images.unsplash.com/photo-1514066558159-fc8c737ef259?crop=entropy&cs=srgb&fm=jpg&ixid=MXw3MjAxN3wwfDF8c2VhcmNofDMxfHxjb2ZmZWV8ZW58MHx8fA&ixlib=rb-1.2.1&q=85&q=85&fmt=jpg&crop=entropy&cs=tinysrgb&w=450",
                "text": "Un equipo extraordinario que te hará pasar un momento inolvidable.",
                "btnHTML": "Conocer"
            }]
        }
    }
    

    render(){
        return(
            <main className="wrapper">
                <CardsList
                    items={this.state.cards}
                />
                <BoxesList/>
                <PostList 
                    products={this.state.data}
                />
            </main>
        )
    }
}

export default Main