import React from 'react'
import Boxes from './boxes'

class BoxesList extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            data: [{
                "id": 1,
                "title": "SOURCING THE BEST SINGLE ESTATE COFFEES",
                "text": "We source the best single estate coffees from around the world. Over the last 10 years, we have acquired the individual crops of over 200 single estate farms from places such as Rwanda, Kenya, Ethiopia, Zambia, Colombia, and Guatemala."
            }],
        }
    }
    render(){
        return(
            <article id="boxesContainer">
                { this.state.data.map((item) => {
                        return(
                            <Boxes
                                title={item.title}
                                text={item.text}
                            />
                        )
                    })
                }
            </article>
        )
    }
}

export default BoxesList